import React from 'react'
import "./Entrypoint.scss"
import { Link } from "react-router-dom"
import { useHistory } from "react-router-dom";

const Entrypoint = ({circleFade}) => {
    const history = useHistory();

    return (
        <div id="entrypoint-section">
            <div id="content">
                <div className="avatar"
                    style={{backgroundImage:`url("/asset/images/avatar.png")`}}>
                </div>
                <div className="greet-section">
                    <h1 className="greet-text">嗨！我是信宏</h1>
                </div>
                <a className="abouts-btn"
                    onClick={()=>{
                        circleFade()
                        let timer = setTimeout(()=>{
                            history.push("/resume");
                            clearTimeout(timer);
                        },1000)
                    }}
                    >
                    <div className="left"></div>
                    <h1>About me </h1>
                    <div className="right"></div>
                </a>
                <div className="prompt">☝ click</div>
            </div>

        </div>

    )
}

export default Entrypoint
