import React from 'react'
import "./Navigation.scss"
import { useHistory } from "react-router-dom";
import {FaBars} from "react-icons/fa";
import { Link } from "react-router-dom"

const TopNavbar = ({}) => {
    const history = useHistory();

    const navList = [
        {
            title:"Introduction 📝",
            link:"#intro-section"
        },
        {
            title:"Experience 🎓 & 🏢",
            link:"#experience-intro-section"
        },

        {
            title:"Portifolio 🤖",
            link:"#portfolio-section"
        },
        {
            title:"Contact me 🙂",
            link:"#contact-section"
        },
    ]
    return (
        <div id="top-navigation-section">
            <div className="top">
                <Link to="/">
                    <div className="avatar"
                        style={{backgroundImage:`url("/asset/images/avatar.png")`}}>
                    </div>
                </Link>

                <input id="collapse-checked" className="collapse-checked" type="checkbox"
                    onChange={(e)=>{
                        if(e.target.checked){
                            document.getElementById("menu").style.height = "auto"
                        }else{
                            document.getElementById("menu").style.height = "0px"
                        }   
                    }}
                
                ></input>
                <span className="collapse-icon"><FaBars/></span>
            </div>
            <div id="menu" className="menu">
                {navList.map((nav,index)=>{
                    return(
                        <a href={nav.link} key={index} className="menu-item"
                            onClick={()=>{
                                document.getElementById("collapse-checked").checked = false;
                                document.getElementById("menu").style.height = "0px"
                                // let timer = setTimeout(()=>{
                                //     history.push(`${nav.link}`);
                                //     clearTimeout(timer);
                                // },1000)
                            }}
                        >
                            {nav.title}
                        </a>
                    )
                })}
            </div>
        </div>
    )
}

export default TopNavbar
