import React from 'react'
import "./Navigation.scss"
import { useHistory } from "react-router-dom";

const MainNavbar = ({circleFade}) => {
    const history = useHistory();

    const navList = [
        {
            title:"Experience 🎓 & 🏢",
            link:"/experience"
        },
        {
            title:"Introduction 📝",
            link:""
        },
        {
            title:"Portifolio 🤖",
            link:""
        },
        {
            title:"Contact me 🙂",
            link:""
        },
    ]
    return (
        <div id="main-navigation-section">
            <div className="content">
                {navList.map((nav,index)=>{
                    return(
                        <a to={nav.link} key={index} className="nav"
                            onClick={()=>{
                                circleFade()
                                let timer = setTimeout(()=>{
                                    history.push(`${nav.link}`);
                                    clearTimeout(timer);
                                },1000)
                            }}
                        >
                            <h1>{nav.title}</h1>
                        </a>
                    )
                })}
            </div>
        </div>
    )
}

export default MainNavbar
