import React, { useState } from 'react'
import "./Carousel.scss"
import { Link } from "react-router-dom"

    
const carouselList = [
    {
        title:"雄才大智",
        summary:"",
        src:"asset/images/carousel/雄才.jpg"
    },
    {
        title:"資料庫學期專案",
        summary:"",
        src:"asset/images/carousel/資料庫專案.png"
    },
    {
        title:"系上專題",
        summary:"",
        src:"asset/images/carousel/NFU_project.jpg"
    },
    {
        title:"PDEV",
        summary:"",
        src:"asset/images/carousel/PDEV.jpg"
    }
]


const CarouselItem = ({carouselItem,selected})=>{
    return(
        <div style={{backgroundImage:`url(${carouselItem.src})`}} className="carousel-item">
        </div>
    )
}

const CarouseList = ({selected})=>{

    return(
        <div className="carousel-list">
            {carouselList.map((item,index)=>{
                return (
                    <div className="carousel-item-sheet" id={`carousel-item-${index}`}
                        data-project={item.title}
                        onClick={()=>{
                            slideRight(item.title)
                        }}
                        >
                        <CarouselItem key={index} carouselItem={item}/>
                    </div>
                )
            })}
        </div>
    )
}

const slideRight = (title)=>{
    let itemList = document.querySelectorAll(".carousel-item-sheet")
    itemList.forEach((item,index)=>{
        let number = parseInt(item.id.split("carousel-item-")[1])
        if((number+1) >= itemList.length){
            number = -1;
        }
        item.id = `carousel-item-${number+1}`;
    })
    if (document.getElementById("carousel-item-0").dataset.project != title){
        slideRight(title)
    }
}

const Carousel = () => {
    const [selected,setSelected] = useState(0)


    return (
        <div className="carousel-section">
            <div className="scene">
                <CarouseList selected={selected}/>
            </div>

        </div>
    )
}

export default Carousel
