import React, { useState } from 'react'
import Entrypoint from "./components/entrypoint/Entrypoint"
import Experience from "./components/experience/Experience"
import MainNavbar from "./components/navigation/MainNavbar"
import Circle from "./components/circle/Circle"
import Resume from "./components/resume/Resume"

import "./Home.scss"
import { Route } from "react-router-dom"
const slideList = [
    {
        elementId: "entrypoint-section",
        translateY: 0
    }
    ,
    {
        elementId: "navigation-section",
        translateY: 0
    }
]


const smoothSlideUp = () => {
    slideList.forEach((data) => {
        data.translateY += 100;
        let element = document.getElementById(data.elementId)
        element.classList.remove("slide-down");
        element.classList.add("slide-up");
        element.style.transform = `translateY(-${data.translateY}vh)`

    })
}


const smoothSlideDown = () => {
    slideList.forEach((data) => {
        data.translateY -= 100;
        let element = document.getElementById(data.elementId)
        element.classList.remove("slide-up");
        element.classList.add("slide-down");
        element.style.transform = `translateY(${data.translateY}vh)`
    })
}



const Home = () => {
    const [showCircle,setShowCircle] = useState(false)
    const circleFade = ()=>{
        document.getElementById("circle-section").classList.add("circle-fade-in-out")
        setTimeout(()=>{
            document.getElementById("circle-section").classList.remove("circle-fade-in-out")
        },2000)
    }
    return (
        <div id="home-section" className="home-section">
            <Circle/>
            <Route exact path="/" 
                render={() => 
                    <Entrypoint circleFade={circleFade} />
                }>    
            </Route>
            <Route exact path="/mainNavbar" 
                render={() => 
                    <MainNavbar circleFade={circleFade} />
                }>    
            </Route>
            <Route exact path="/experience" 
                render={() => 
                    <Experience />
                }>    
            </Route>
            <Route exact path="/resume" 
                render={() => 
                    <Resume />
                }>    
            </Route>


            {/* <Userintro smoothChange={smoothChange}/> */}
        </div>
    )
}

export default Home
